import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Scott({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Инвентарь - Скамья Скотта`} />
      <TopBanner style={scottStyle} title={'Упражнения на скамье Скотта'} />
      {posts
        .filter(post => post.node.frontmatter.filterplace === 'scott')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const scottStyle = {
	backgroundImage: 'url(http://www.fitnesstechno.com/i/catalog/150/713458/713458_pthrf_800_600.jpg?v=147681)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query ScottQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



